@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap);
:root {
  --ff-primary: 'Inter', sans-serif;
  --fc-darkest: #000;
  --fc-darker: rgba(0, 0, 0, 0.8); 
  --fc-dark: #b2b2b2;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-appbar {
	margin-bottom: 10px;
}

.nav-toolbar {
	box-sizing: border-box;
	display: -webkit-flex;
	display: flex;
	background-color: whitesmoke;
	color: black;
}

.nav-toolbar .nav-title {
	font-size: 20px;
}

.nav-link {
	text-decoration: none;
	color: black;
	padding: 0 2vw;
}

.nav-link.active {
	color: darkblue;
}

.nav-rendered-links {
	margin-left: auto;
}

.card {
  width: min(200px, 15%);
  padding: 20px;
  margin: 2%;
  box-shadow: 4px 6px 13px -1px rgba(225, 225, 225, 0.25);
  border-radius: 2px;
}

#card-title {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 600;
  font-size: 16;
  text-align: left;
}

#card-desc {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400;
  font-size: 14;
  text-align: left;
}

#icon {
  display: block;
}

@media (max-width: 768px) {
  .card {
    width: 40%;
    margin: 5px;
    padding: 8px;
  }

  #card-desc {
    font-size: 12;
  }
}

.card-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 4%;
}
.project-page {
    text-align: left;
    text-align: initial;
}

.sidebar {
    display: inline-block;
    transition: width 0.5s;
    width: 0px;
}

.sidebar.expand {
    width: 200px;
}

.togglebutton {
    display: inline-block;
    width: 20px;
    height: 20px;
}

.content-container {
    display: inline-block;
    text-align: center;
    margin-left: 20px;
}

.web-app-modal {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.web-app-modal-content {
  background-color: white;
  border-radius: 4px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 12px;
}
.button-root {
    color: black;
}
.login-form-root {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.login-form-root .login-form-field {
  margin-bottom: 18px;
}
.login-view-root {
  margin: 28px;
}

.login-error {
  color: red;
}

.login-view-title {
  margin-bottom: 28px;
}
.container{
    position: relative;
    min-height: 100vh;
}

.ubc-logo{
    margin-right: 70%;
    padding-bottom: 40px;
    max-width: 100%;
}

.footer{
    background-color: #F2F2F2;
    padding: 40px 120px 2.5rem;
    margin: 0 auto;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}

.footer a{
    text-decoration: none;
    color: black;
}

.footer li{
    list-style: none;

}

.footer-main{
    display: -webkit-flex;
    display: flex;
    margin-bottom: 4rem;
}

.footer-column{
    -webkit-flex-basis: 200px;
            flex-basis: 200px;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.footer-heading{
    margin-bottom: 1rem;
    text-align: left;
}

.footer-link{
    display: block;
    padding: 5px 10px;
    margin: 0 auto;
    text-align: left;
}

.footer-right{
    margin-left: auto;
    display: -webkit-flex;
    display: flex;
    grid-gap: 50px;
    gap: 50px;
}

.horizontalLine {
    border: none;
    border-bottom: 1px solid gainsboro;
    margin-left: 0.5%;
}

.campus-text{
    text-align: left;
    margin-left: 0.5%;
    margin-top: 1%;
}

@media only screen and (max-width: 768px){
    .footer{
        padding: 5px 15px 2.5rem;
    }

    .ubc-logo{
        padding-bottom: 0;
    }
    .footer-main{
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .footer-column-navigate{
        margin-left: 1%;
    }
    .footer-column-lab-info{
        margin-left: 4%;
        margin-top: 0;
    }

    .campus-text{
        margin-top: 1%;
        text-align: left;
        margin-left: 0;
    }
    .footer-column-projects{
        margin-left: 1%;
    }
    .footer-right{
        -webkit-flex-direction: column;
                flex-direction: column;
        margin-left: 0;
        grid-gap: 0;
        gap: 0;
    }

    .footer-link{
        padding: 3px 5px;
    }

    .footer-column-lab-info{
        margin-left: 0;
        padding-top: 0;
    }

    .footer-heading{
        margin-bottom: 0;
    }

}


.App {
  text-align: center;
}

